.container-layout {
  width: 100%;
  height: 100vh;
  margin-bottom: 100px;
  position: relative;
}

.social__card {
  background: rgb(255, 249, 244);
  padding: 5px 0;
  width: 648px;
  margin: 30px auto;
}

.comment_section {
  padding: 5px 0;
  width: 648px;
}

.info__container {
  display: grid;
  grid-template-columns: 60px 200px;
  gap: 3px;
  justify-content: start;
  align-content: start;
  line-height: 4px;
  cursor: pointer;
}

.picture {
  background: black;
  position: relative;
  height: 55px;
  width: 55px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  display: inline-block;
  opacity: 0.6;
}

.picture strong {
  position: absolute;
  top: 23px;
  left: 3px;
}

.posts {
  position: relative;
  margin-top: 10px;
}

.posts__bg {
  background: rgb(255, 30, 0);
  position: relative;
  height: 220px;
}

.posts__picture {
  background: black;
  position: absolute;
  top: 5px;
  left: 5px;
  height: 52px;
  width: 52px;
  display: inline-block;
}

.posts__picture strong {
  position: absolute;
  top: 12px;
  left: 1px;
  color: white;
}

.posts__heading {
  position: absolute;
  width: 100%;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.user {
  position: absolute;
  bottom: 5px;
  right: 5px;
  width: 150px;
  background: white;
  display: flex;
  padding-left: 5px;
  align-items: center;
}

.user__name {
  color: rgb(255, 30, 0);
  font-weight: bold;
}

.user__pic {
 padding-left: 10px;
}

.user__pic img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  object-fit: cover;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.posts__preview {
  border: 1px outset rgb(185, 178, 178);
  text-align: center;
}

.horizontal__line {
 border: 2px double red;
 width: 50px;
 margin-left: auto;
 margin-right: auto;
}

.posts__title {
  font-size: 17px;
  font-weight: bold;
}

.link {
  font-size: 16px;
  opacity: 0.7;
  /* margin-top: -15px; */
  cursor: default;
}

.engage {
  position: relative;
  display: flex;
}

.quote code {
 display: block;
}

.modify {
  position: absolute;
  bottom: 0;
  margin: 3px auto;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 6px 0;
}

.comment,
.like,
.retweet {
  flex-basis: 50px;
}

.fa-trash-o,
.fa-pencil {
  flex-basis: 30px;
  margin-right: 10px;
}

.fa-trash-o:hover,
.fa-pencil:hover {
  opacity: 0.8;
}

.count {
  margin-left: 4px;
  margin-bottom: 10px;
}

.commentbox {
  box-shadow: 0 2px 4px 1px gainsboro;
  border: 2px solid white;
  border-bottom: 5px inset #d8dee9;
  padding: 1rem;
  color: grey;
  border-left: 3px outset rgb(255, 30, 0);
  resize: horizontal;
  width: 100%;
}

.commentText {
  position: relative;
  box-shadow: 0 2px 2px 2px gainsboro;
  padding: 20px 0;
  margin-bottom: 10px;
  width: 100%;
}

@media(max-width: 660px){
 .social__card {
  width: 100%;
 }

 .container-layout {
 padding-left: 15px;
 padding-right: 15px;
}

.comment_section {
 padding: 5px 0;
 width: 100%;
}

}